"use client";

import { useState, useEffect } from "react";
import { settingsAPI } from "@/lib/api";
import type { CreatorProfile } from "@/lib/api";

type Tab = "prompts" | "profiles" | "templates" | "channels" | "simulators" | "agent" | "logs";

